import React, { Component } from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';
import { Container, Collapse, Navbar, NavbarToggler, Nav, NavItem } from 'reactstrap';
import logo from '../../assets/img/logo.png'
import { Authenticate } from './../../services';
import { connect } from 'react-redux';



const authenticate = new Authenticate()
class TopHeader extends Component {
    state = {
        isOpen: false,
        loggedIn: false
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.auth.loggedIn === prevState.loggedIn) return null
        return {
            loggedIn: nextProps.auth.loggedIn,
        }
    }


    toggle = () => {
        this.setState(prevState => {
            return { isOpen: !prevState.isOpen }
        });
    }

    logoutHandler = async () => {
        await authenticate.logoutHandler(this.props.history)
    }

    render() {
        let { wheelPosition: { clientY, deltaY } } = this.props
        let { isOpen, loggedIn } = this.state

        return (
            <header id="header">
                <div className={(clientY > 100 && deltaY > 100) ? 'sticky-nav nav-wrapper' : 'nav-wrapper'} >
                    <Container className='p-0'>
                        <Navbar expand="md p-0">
                            <Link className='text-dark navbar-brand' to='/' style={{ color: '#eaeaea' }}>
                                <img src={logo} alt='logo' className='site-logo' /> Arrot Krishi Ponno Limited
                        </Link>
                            <NavbarToggler onClick={this.toggle}><i className='fa fa-bars' /></NavbarToggler>
                            <Collapse isOpen={isOpen} navbar>
                                <Nav className="ml-auto" navbar>
                                    <NavItem>
                                        <Link to="/about" className="nav-link">About</Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/growers" className="nav-link">Growers</Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/buyers" className="nav-link">Buyers</Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/why-arrot" className="nav-link">Why Arrot Vegitables</Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/team" className="nav-link">Team</Link>
                                    </NavItem>
                                    <NavItem>
                                        <Link to="/contact" className="nav-link">Contact</Link>
                                    </NavItem>
                                    {loggedIn ?
                                        <>
                                            <NavItem >
                                                <Link to="/order" className="nav-link">Order</Link>
                                            </NavItem>
                                            <NavItem onClick={() => this.logoutHandler()}>
                                                <Link to="#" className="nav-link">Logout</Link>
                                            </NavItem>
                                        </> :
                                        <NavItem>
                                            <Link to="/login" className="nav-link">Login</Link>
                                        </NavItem>}
                                </Nav>
                            </Collapse>
                        </Navbar>
                    </Container>
                </div>
            </header>
        )
    }
}

TopHeader.propTypes = {
    history: PropTypes.object.isRequired,
    wheelPosition: PropTypes.object
}

const mapStateToProps = state => ({
    auth: state.auth
})
export default connect(mapStateToProps)(TopHeader) 