import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, ModalFooter, Table } from "reactstrap";
import { ProductAction, OrderAction } from "../../services";
import dateFormat from 'dateformat';
import { decimalNumber } from "../../utils/helper";
import PropTypes from 'prop-types'



const productAction = new ProductAction()
const orderAction = new OrderAction()

class OrderPlaceModal extends Component {
    state = {
        isLoading: true,
        deliveryDate: ' ',
        products: [],
    }


    async componentDidMount() {
        let { data } = await productAction.get()
        this.setState({ isLoading: false, products: data ? data.results : [] })
    }

    changeHandler(e, id) {
        let input = parseInt(e.target.value)
        if (input < 0)
            return null

        this.setState(prevState => {
            let { products } = prevState

            let pdt = products.map(item => {
                let quantity = item.quantity || 0
                if (item.id === id) {
                    quantity = e.target.value
                }

                return {
                    ...item,
                    quantity: parseInt(quantity)
                }
            })
            return {
                products: pdt
            }
        })
    }


    submitHandler = async () => {
        let { products, deliveryDate } = this.state

        let totalPrice = 0;
        let order_items = []
        products.map(item => {
            if (parseInt(item.quantity) > 0) {
                let pdt = {
                    product: item.id,
                    price: item.unit_rate,
                    unit: item.unit,
                    quantity: decimalNumber(item.quantity),
                }
                totalPrice += parseInt(item.unit_rate) * parseInt(item.quantity)
                order_items.push(pdt)
            }
        })


        if (order_items.length === 0) {
            alert('Please select any product to place order!!!')
            return null;
        } else if (!deliveryDate || deliveryDate === '' || deliveryDate === ' ') {
            alert('Please select delivery date!!!')
            return null;
        }


        let formData = {
            order_items,
            delivery_date: dateFormat(deliveryDate, "yyyy-mm-dd"),
            order_date: dateFormat(new Date(), "yyyy-mm-dd")
        }
        let { isSuccess, data } = await orderAction.submitOrder(formData)

        this.setState({ isLoading: false })
        if (isSuccess) {
            let totalOrders = this.props.orders || []
            data.total_amount = decimalNumber(totalPrice)
            totalOrders.unshift(data)

            this.props.dataSetToState(totalOrders)
            this.props.modalHandler()
        }
    }




    render() {
        let { modalIsOpen, modalHandler } = this.props;
        let { products, deliveryDate, isLoading } = this.state

        const getTotalPrice = () => {
            let totalPrice = 0
            if (products.length) {
                products.map(item => {
                    let quantity = parseInt(item.quantity) || 0
                    let price = parseInt(item.unit_rate)

                    if (quantity > 0) {
                        totalPrice += price * quantity
                    }
                })
            }

            return totalPrice;
        }

        return (
            <Modal
                size="xl"
                dialogClassName="modal-90w"
                show={modalIsOpen}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>Pick Product From List</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Table responsive striped bordered hover size="sm" className="text-center">
                        <thead>
                            <tr>
                                <th width="15">ID</th>
                                <th>Code</th>
                                <th>Product Name</th>
                                <th>Unit</th>
                                <th>Rate (TK)</th>
                                <th>Quantity</th>
                                <th>Total (TK)</th>
                            </tr>
                        </thead>
                        <tbody>
                            {isLoading ? <tr>
                                <td colSpan={10}>Loading...</td>
                            </tr> : products.length > 0 && products.map(item => <tr key={item.id}>
                                <td scope="row">{item.id}</td>
                                <td>{item.product_code}</td>
                                <td>{item.name}</td>
                                <td>{item.unit}</td>
                                <td>{item.unit_rate}</td>
                                <td width="100"><Form.Control type="number" size="sm" name="quantity" onChange={(e) => this.changeHandler(e, item.id)} value={item.quantity || 0} /></td>
                                <td>{parseInt(item.unit_rate) * parseInt(item.quantity || 1)}</td>
                            </tr>)}

                            <tr>
                                <td colSpan={2}>Delivery Date:<span className="text-danger">*</span></td>
                                <td colSpan={1}><Form.Control type="date" size="sm" name="dateTo" value={deliveryDate} required onChange={(e) => this.setState({ deliveryDate: e.target.value })} /></td>
                                <td colSpan={4}>Grand Total: {getTotalPrice()}</td>
                            </tr>
                        </tbody>
                    </Table>
                </Modal.Body>
                <ModalFooter>
                    <Button color="warning" onClick={() => window.confirm('Are you sure?') && this.submitHandler()} disabled={isLoading}>Place Order</Button>{' '}
                    <Button color="secondary" onClick={() => modalHandler()} disabled={isLoading}>Cancel</Button>
                </ModalFooter>
            </Modal>
        )
    }
}


OrderPlaceModal.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    dataSetToState: PropTypes.func.isRequired,
    modalHandler: PropTypes.func.isRequired,
    modalIsOpen: PropTypes.bool,
    orders: PropTypes.array,
}


export default OrderPlaceModal;
