import React, { Component } from "react";
import { Card, Container } from "react-bootstrap";
import OrderPlaceModal from "./OrderPlaceModal";
import OrderHistory from "./OrderHistory";
import Search from "./Search";
import { OrderAction } from '../../services';


const orderAction = new OrderAction()
class Order extends Component {
    state = {
        orders: [],
        modalIsOpen: false,
        isLoading: false,
    }

    async componentDidMount() {
        this.setState({ isLoading: true })
        let { data } = await orderAction.get()
        this.dataSetToState(data.results)
    }

    dataSetToState = (orders = []) => {
        this.setState({ isLoading: false, orders })
    }


    modalHandler = () => {
        this.setState(prevState => ({
            modalIsOpen: !prevState.modalIsOpen,
        }));
    }

    handleChange(event) {
        event.preventDefault();
        this.setState({ [event.target.name]: event.target.value });
    }

    render() {
        let { modalIsOpen, orders, isLoading } = this.state

        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <h5><u>Order History</u></h5>
                    </Card.Header>
                    <Card.Body>
                        <Search modalHandler={this.modalHandler} />
                        <OrderHistory orders={orders} isLoading={isLoading} />
                    </Card.Body>
                </Card>

                <OrderPlaceModal
                    modalIsOpen={modalIsOpen}
                    orders={orders}
                    modalHandler={this.modalHandler}
                    dataSetToState={this.dataSetToState}
                />
            </Container>
        )
    }
}

export default Order;
