import React, { Component } from 'react'
import { Row, Col, Button, Input } from 'reactstrap';
import { Form, InputGroup } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faFilter } from "@fortawesome/free-solid-svg-icons";
import PropTypes from 'prop-types'




class Search extends Component {
    render() {
        let { modalHandler } = this.props
        return (
            <>
                <Row>
                    <Col className="text-left mb-2" md={2}>
                        <InputGroup>
                            <Form.Control type="text" placeholder="Search" size="sm" name="search" />
                        </InputGroup>
                    </Col>
                    <Col md={2}>
                        <Form.Control type="date" size="sm" name="dateFrom" />
                    </Col>
                    <Col md={2}>
                        <Form.Control type="date" size="sm" name="dateTo" />
                    </Col>
                    <Col md={1}>
                        <Button variant="primary" size="sm">
                            <FontAwesomeIcon icon={faFilter} />
                        </Button>
                    </Col>
                    <Col className="text-right">
                        <Button color="warning" size="sm" onClick={() => modalHandler()}>
                            <FontAwesomeIcon icon={faPlus} /> &nbsp; Place A Order
                                </Button>
                    </Col>
                </Row>
            </>
        )
    }
}


Search.propTypes = {
    modalHandler: PropTypes.func.isRequired,
}


export default Search